import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
const AboutPage = ({ data }) => {
  return (
    <Layout url={data.allContentstackGatsbyabout.edges[0].node.url}>
      <SEO title="About Us" />
      <div>
        <h1>{data.allContentstackGatsbyabout.edges[0].node.title}</h1>
        <p>{data.allContentstackGatsbyabout.edges[0].node.multi_line}</p>
      </div>
    </Layout>
  )
}

export const pageQuery = graphql`
  {
    allContentstackGatsbyabout {
      edges {
        node {
          title
          multi_line
          url
        }
      }
    }
  }
`
export default AboutPage
